body {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
  /* background-color: #355070; */
  background: rgb(0, 0, 0);
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(73, 95, 160, 1) 0%,
    rgba(115, 118, 181, 1) 100%
  );
  overflow: hidden;
}
.app {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.title {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
